import { PropertyConfigItem } from "./PropertyConfigItem";

/**
 * 属性分组
 */
export class PropertyConfigGroup {
    /**
     * 分组ID
     */
    groupId: string;
    /**
     * 分组名
     */
    groupName: string;
    /**
     * 属性明细列表
     */
    items: PropertyConfigItem[] = [];

    constructor(groupId?: string, groupName?: string) {
        this.groupId = groupId;
        this.groupName = groupName;
    }
}